// SCSS variables are information about icon's compiled state, stored under its original file name
//
// .icon-home {
//   width: $icon-home-width;
// }
//
// The large array-like variables contain all information about a single icon
// $icon-home: x y offset_x offset_y width height total_width total_height image_path;
//
// At the bottom of this section, we provide information about the spritesheet itself
// $spritesheet: width height image $spritesheet-sprites;
$sprite-facebook-name: 'sprite-facebook';
$sprite-facebook-x: 0px;
$sprite-facebook-y: 19px;
$sprite-facebook-offset-x: 0px;
$sprite-facebook-offset-y: -19px;
$sprite-facebook-width: 11px;
$sprite-facebook-height: 20px;
$sprite-facebook-total-width: 41px;
$sprite-facebook-total-height: 39px;
$sprite-facebook-image: '../img/sprite-0880087523603746.png';
$sprite-facebook: (0px, 19px, 0px, -19px, 11px, 20px, 41px, 39px, '../img/sprite-0880087523603746.png', 'sprite-facebook', );
$sprite-telegram-name: 'sprite-telegram';
$sprite-telegram-x: 23px;
$sprite-telegram-y: 0px;
$sprite-telegram-offset-x: -23px;
$sprite-telegram-offset-y: 0px;
$sprite-telegram-width: 18px;
$sprite-telegram-height: 15px;
$sprite-telegram-total-width: 41px;
$sprite-telegram-total-height: 39px;
$sprite-telegram-image: '../img/sprite-0880087523603746.png';
$sprite-telegram: (23px, 0px, -23px, 0px, 18px, 15px, 41px, 39px, '../img/sprite-0880087523603746.png', 'sprite-telegram', );
$sprite-twitter-name: 'sprite-twitter';
$sprite-twitter-x: 0px;
$sprite-twitter-y: 0px;
$sprite-twitter-offset-x: 0px;
$sprite-twitter-offset-y: 0px;
$sprite-twitter-width: 19px;
$sprite-twitter-height: 15px;
$sprite-twitter-total-width: 41px;
$sprite-twitter-total-height: 39px;
$sprite-twitter-image: '../img/sprite-0880087523603746.png';
$sprite-twitter: (0px, 0px, 0px, 0px, 19px, 15px, 41px, 39px, '../img/sprite-0880087523603746.png', 'sprite-twitter', );
$spritesheet-width: 41px;
$spritesheet-height: 39px;
$spritesheet-image: '../img/sprite-0880087523603746.png';
$spritesheet-sprites: ($sprite-facebook, $sprite-telegram, $sprite-twitter, );
$spritesheet: (41px, 39px, '../img/sprite-0880087523603746.png', $spritesheet-sprites, );

// The provided mixins are intended to be used with the array-like variables
//
// .icon-home {
//   @include sprite-width($icon-home);
// }
//
// .icon-email {
//   @include sprite($icon-email);
// }
//
// Example usage in HTML:
//
// `display: block` sprite:
// <div class="icon-home"></div>
//
// To change `display` (e.g. `display: inline-block;`), we suggest using a common CSS class:
//
// // CSS
// .icon {
//   display: inline-block;
// }
//
// // HTML
// <i class="icon icon-home"></i>
@mixin sprite-width($sprite) {
  width: nth($sprite, 5);
}

@mixin sprite-height($sprite) {
  height: nth($sprite, 6);
}

@mixin sprite-position($sprite) {
  $sprite-offset-x: nth($sprite, 3);
  $sprite-offset-y: nth($sprite, 4);
  background-position: $sprite-offset-x  $sprite-offset-y;
}

@mixin sprite-image($sprite) {
  $sprite-image: nth($sprite, 9);
  background-image: url(#{$sprite-image});
}

@mixin sprite($sprite) {
  @include sprite-image($sprite);
  @include sprite-position($sprite);
  @include sprite-width($sprite);
  @include sprite-height($sprite);
}

// The `sprites` mixin generates identical output to the CSS template
//   but can be overridden inside of SCSS
//
// @include sprites($spritesheet-sprites);
@mixin sprites($sprites) {
  @each $sprite in $sprites {
    $sprite-name: nth($sprite, 10);
    .#{$sprite-name} {
      @include sprite($sprite);
    }
  }
}
